import { createAction } from 'typesafe-actions';

interface ICard {
  id: string;
  name: string;
  caption: string;
  image: string;
}

export const stepAction = createAction('cards/STEP', (action) => {
  // Note: "action" callback does not need "type" parameter
  return (opponentCard: ICard, playerCard: ICard) => action({ opponentCard, playerCard });
});

export const drawAction = createAction('cards/DRAW', (action) => () => action());
