import {ActionType, getType} from "typesafe-actions";
import {IBaseState} from "./baseModule";
import {gms} from './gms'
import {dissoc, sortBy, compose, fromPairs, toPairs, nth, map} from "ramda";
import {drawAction, stepAction} from "./stepActionCreators";
import messages from './messages';

interface ICard {
  id: string;
  name: string;
  image: string;
  caption: string
}

function shuffle<T>(array: T[]) {
  return sortBy(() => Math.random() - 0.5)(array)
}


export interface ITableState extends IBaseState {
  opponentCard: ICard;
  clickable: boolean;
  unused: ICard[],
  toast?: {header: string, caption: string};
  playerCard?: ICard;

}

const initialState: () => ITableState = () => {
  const source: ICard[] = compose(shuffle, map(nth(1)), toPairs)(gms) as ICard[];
  const opponentCard: ICard = source.pop();
  return {
    opponentCard,
    unused: source,
    clickable: false,
    error: "",
    loaded: false,
    pending: false
  }
};

export function tableReducer(
  state: ITableState = initialState(),
  action: ActionType<any>
): ITableState {
  switch (action.type) {
    // case getType(counterActionCreators.increment):
    //   return {
    //     ...state,
    //     count: state.count + 1
    //   };
    // case getType(counterActionCreators.decrement):
    //   return {
    //     ...state,
    //     count: ((state.count - 1 > 0) ? state.count - 1 : 0)
    //   };
    case getType(stepAction):
      const result = messages(action.payload.opponentCard.id, action.payload.playerCard.id);
      return {
        ...state,
        playerCard: action.payload.playerCard,
        clickable: true,
        toast: {
          header: result.success ? '' : '',
          caption: result.message
        }
      };

    case getType(drawAction):
      const {unused} = state;
      const opponentCard = unused.pop();
      return compose(dissoc('playerCard'), dissoc('toast'))({
        ...state,
        clickable: false,
        opponentCard,
        unused
      });
    default:
      return state;
  }
}
