import * as React from "react";
import {Col} from 'react-bootstrap';

interface ICurrentCardsProps extends React.HTMLProps<HTMLDivElement> {
  opponent: string;
  player?: string;
  draw: () => void;
}

export class CurrentCards extends React.Component<ICurrentCardsProps> {
  public render(): JSX.Element {
    const {opponent, player, draw} = this.props;
    return (
      <Col xs={4} onClick={draw}>
        <img src={opponent} className="playing-card-image opponent-card"/>
        {this.renderPlayerCard(player)}
      </Col>
    );
  }

  renderPlayerCard(playerCardImage?: string) {
    if (playerCardImage)
    {return (<img src={playerCardImage} className="playing-card-image player-card"/>)}
  }
}
