import {ActionType, getType} from "typesafe-actions";
import {closeAction} from "./modalActionCreators";


export interface IIntroState {
  show: boolean;
}

const initialState: IIntroState = {
  show: true
};

export function introReducer(
  state: IIntroState = initialState,
  action: ActionType<any>
): IIntroState {
  switch (action.type) {
    case getType(closeAction):
      return {show: false};
    default:
      return state;
  }
}
