import * as React from "react";
import {Col} from 'react-bootstrap';
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";

interface IOutroProps extends React.HTMLProps<HTMLDivElement> {
  show: boolean;
  title: string;
  caption: string;
}

export class Outro extends React.Component<IOutroProps> {
  public render(): JSX.Element {
    const {show, title, caption} = this.props;
    return (
      <Modal show={show}>
        <Modal.Header closeButton>
          <Modal.Title className="text-center">{title}</Modal.Title>
        </Modal.Header>

        <Modal.Body className="text-left">
          {caption}
        </Modal.Body>

        <Modal.Footer>
          <Button onClick={() => window.location.reload()} variant="primary" block > </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
