import * as React from "react";
import {connect} from "react-redux";
import {IStore} from "../redux/IStore";
import Row from "react-bootstrap/Row";
import Col from 'react-bootstrap/Col'
import {CurrentCards} from "../components/CurrentCards";
import {Toast} from "../components/Toast";
import {OpponentCaption} from "../components/OpponentCaption";
import {drawAction, stepAction} from "../redux/modules/stepActionCreators";
import {Dispatch} from "redux";


interface ICard {
  image: string;
  name: string;
  caption: string;
}
interface IToast {
  header: string;
  caption: string
}
interface IStateToProps {
  opponentCard: ICard;
  playerCard?: ICard;
  clickable: boolean;
  toast?: IToast
}
interface IDispatchToProps {
  draw: () => void;
}
interface IProps extends IDispatchToProps, IStateToProps {}

class Table extends React.Component<IProps> {
  public render(): JSX.Element {
    const {opponentCard, toast, playerCard, draw} = this.props;
    return (
      <Row className="justify-content-center align-items-start my-4" xs={6} lg={6}>
        {this.renderToast(toast)}
        <CurrentCards key="opponent-card" opponent={opponentCard.image} player={playerCard?.image} draw={draw}/>
        <OpponentCaption name={opponentCard.name} caption={opponentCard.caption}/>
      </Row>
    );
  }

  renderToast(toast?: IToast): JSX.Element | null {
    if (toast) {
      const {header, caption} = toast;
      return (<Toast header={header} caption={caption}/>)
    }
    else {
      return (<Col xs={2}/>)
    }
  }
}

function mapStateToProps(state: Pick<IStore, "table">): IStateToProps {
  //TODO selector??
  return state.table;
}


const mapDispatchToProps: (Dispatch) => IDispatchToProps = (dispatch: Dispatch) => {
  return {
    draw: () => dispatch(drawAction())
  }
};


const connected = connect(mapStateToProps, mapDispatchToProps)(Table);
export {connected as Table, Table as UnconnectedTable, mapStateToProps};
