import * as React from "react";
import {connect} from "react-redux";
import {range} from 'ramda'
import {IStore} from "../redux/IStore";
import Row from "react-bootstrap/Row";
import {Shirt} from '../components/Shirt'


interface IStateToProps {
  count: number
}

class OpponentDeck extends React.Component<IStateToProps> {
  public render(): JSX.Element {
    const {count} = this.props;
    return (
      <Row className="justify-content-center align-items-start mb-3" xs={8} lg={6}>
        {range(1, count + 1).map((i) => <Shirt key={`shirt${i}`}/>)}
      </Row>
    );
  }
}

function mapStateToProps(state: Pick<IStore, "opponentDeck">): IStateToProps {
  //TODO selector??
  return state.opponentDeck;
}

const connected = connect(mapStateToProps)(OpponentDeck);
export {connected as OpponentDeck, OpponentDeck as UnconnectedOpponentDeck, mapStateToProps};
