import * as React from "react";
import {Col} from 'react-bootstrap';

interface ICardProps extends React.HTMLProps<HTMLDivElement> {
  image: string;
  name: string;
  caption: string;
  onClick: () => void;
}

export class PlayingCard extends React.Component<ICardProps> {
  public render(): JSX.Element {
    const {image, name, caption, onClick} = this.props;
    return (
      <Col>
        <img src={image} className="playing-card-image" onClick={onClick} />
        <p className="playing-card-title">{name}</p>
        <p className="playing-card-caption">{caption}</p>
      </Col>
    );
  }
}
