import {combineReducers, Reducer} from "redux";
import {IStore} from "./IStore";
import {counterReducer} from "./modules/counterModule";
import {opponentDeckReducer} from './modules/opponentDeckModule';
import {deckReducer} from "./modules/deckModule";
import {tableReducer} from "./modules/tableModule";
import {introReducer, modalReducer} from "./modules/modalModule";

const rootReducer: Reducer<IStore> = combineReducers<IStore>({
  table: tableReducer,
  deck: deckReducer,
  opponentDeck: opponentDeckReducer,
  counter: counterReducer,
  intro: introReducer,
  // router: router5Reducer,
  // settings: settingsReducer,
  // stars: starsReducer
});

export default rootReducer;
