import {ActionType, getType} from "typesafe-actions";
import {drawAction, stepAction} from "./stepActionCreators";
import messages from "./messages";

export interface ICounterState {
  winCount: number;
  stepsCount: number;
  finished: boolean;
  jbb: boolean;
  message?: IMessage
}

interface IMessage {
  title: string;
  caption: string
}

const jbbMessage: IMessage = {
  title: '',
  caption: '   ,       .'
};
const results: IMessage[] = [{
  title: ' ',
  caption: '    ?    .'
},
{
  title: ' ',
  caption: '      . '
},
{
  title: ' ',
  caption: ' .        .'
},
{
  title: ' ',
  caption: '   ,   .'
}
];

const initialState: ICounterState = {
  winCount: 0,
  stepsCount: 0,
  finished: false,
  jbb: false
};

export function counterReducer(
  state: ICounterState = initialState,
  action: ActionType<any>
): ICounterState {
  switch (action.type) {
    case getType(stepAction):
      const result = messages(action.payload.opponentCard.id, action.payload.playerCard.id);
      const win = result.success ? 1 : 0;
      const jbb = action.payload.playerCard.id === 'jbb';
      return {
        ...state,
        winCount: state.winCount + win,
        stepsCount: state.stepsCount + 1,
        jbb
      };
    case getType(drawAction):
      if (state.jbb)
      {return {
        ...state,
        finished: true,
        message: jbbMessage
      };}
      else if (state.stepsCount === 3)
      {return {
        ...state,
        finished: true,
        message: results[state.winCount]
      };}
      else
      {return state}
    default:
      return state;
  }
}
