import * as React from "react";
import {connect} from "react-redux";
import {IStore} from "../redux/IStore";
import Row from "react-bootstrap/Row";
import {PlayingCard} from "../components/PlayingCard";
import {Dispatch} from "redux";
import {stepAction} from '../redux/modules/stepActionCreators';


interface ICard {
  id: string
  image: string;
  name: string;
  caption: string;
}
interface IStateToProps {
  opponentCard: ICard;
  clickable: boolean;
  cards: ICard[];
}

interface IProps extends IStateToProps, IDispatchToProps {}

interface IDispatchToProps {
  step(opponentCard: ICard, playerCard: ICard): void
}

class Deck extends React.Component<IProps> {
  public render(): JSX.Element {
    const {cards, step, opponentCard, clickable} = this.props;
    return (
      <Row className="justify-content-center align-items-start mb-3" xs={8} lg={6}>
        {cards.map((card) =>
          <PlayingCard
            key={`card_${card.name}`}
            name={card.name}
            caption={card.caption}
            image={card.image}
            onClick={() => {
              if(clickable) {step(opponentCard, card)}
            }}/>)}
      </Row>
    );
  }
}

function mapStateToProps(state: Pick<IStore, "deck" | 'table'>): IStateToProps {
  //TODO selector??
  return {
    cards: state.deck.cards,
    clickable: state.deck.clickable,
    opponentCard: state.table.opponentCard
  }
}

const mapDispatchToProps: (Dispatch) => IDispatchToProps = (dispatch: Dispatch) => {
  return {
    step: (opponentCard: ICard, playerCard: ICard) => dispatch(stepAction(opponentCard, playerCard))
  }
};

const connected = connect(mapStateToProps, mapDispatchToProps)(Deck);
export {connected as Deck, Deck as UnconnectedOpponentDeck, mapStateToProps, mapDispatchToProps};
