import {ActionType, getType} from "typesafe-actions";
import {IBaseState} from "./baseModule";
import {coaches} from "./coaches";
import {drawAction, stepAction} from "./stepActionCreators";
import {sortBy, drop, compose, toPairs, map, nth, take} from "ramda";


//TODO DRY
function shuffle<T>(array: T[]) {
  return sortBy(() => Math.random() - 0.5)(array)
}

interface ICard {
  id: string,
  name: string,
  image: string,
  caption: string
}

export interface IDeckState extends IBaseState {
  cards: ICard[];
  unused: ICard[];
  clickable: boolean;
}

const initialState: () => IDeckState = () => {
  const source: ICard[] = (compose(map(nth(1)), shuffle, toPairs)(coaches)) as ICard[];
  const cards: ICard[] = take(3)(source);
  const unused: ICard[] = drop(3)(source) as ICard[];
  return {
    cards,
    unused,
    clickable: true,
    error: "",
    loaded: false,
    pending: false
  }
};

export function deckReducer(
  state: IDeckState = initialState(),
  action: ActionType<any>
): IDeckState {
  switch (action.type) {
    // case getType(counterActionCreators.increment):
    //   return {
    //     ...state,
    //     count: state.count + 1
    //   };
    case getType(stepAction):
      return {
        ...state,
        clickable: false,
        cards:
          state.cards.filter((card) => card.id !== action.payload.playerCard.id),
      };

    case getType(drawAction):
      const {unused} = state;
      const newCard = unused.pop();
      return {
        ...state,
        clickable: true,
        unused,
        cards:
          [...state.cards, newCard],
      };
      // return {
      //   ...state,
      //   count: ((state.count - 1 > 0) ? state.count - 1 : 0)
      // };
    default:
      return state;
  }
}
