const failMessages = [
  '.    .      ?',
  '.   ,     ,      .',
  '.       ,    .'
];


const registry = {
  morey: {
    tibs: ' !          ',
    fitz: ' !     .    .',
    lue: ' !     ,   .'
  },
  shlenk: {
    pitino: ' !         .',
    jerger: ' !          .',
    mj: ' !        .   .',
    kenny: ' !     .   .'
  },
  rose: {
    mj: ' !     ,      ',
    kenny: ' !     .   .',
    jbb: ' !   ,      ,     .',
    pitino: ' !     .        .',
    jerger: ' !    .        .    .'
  },
  marks: {
    tibs: ' !          ',
    fitz: ' !    ,     5G',
    lue: ' !         .'
  },
  divac: {
    tibs: ' !       ,  ',
    jvg: ' !    ,      ',
    jbb: ' !   ,      ,     .',
    lue: ' !     ,     .'
  },
  kobe: {
    mj: ' !     ,    c    ',
    jbb: ' !   ,      ,     .',
    pitino: ' !    -   ,    .',
    fitz: ' !      .'
  },
  grif: {
    jvg: ' !     ,   ,  ',
    kenny: ' !              .',
    jerger: ' !          .',
  },
  rosas: {
    jvg: ' !      ',
    jerger: ' !          .',
    lue: ' !   ,          .'
  },
  brand: {
    tibs: ' !    1  .      .',
    jvg: ' !   ,     3- ',
    lue: ' !       ,   0-4'
  }
};

export default (gm: string, coach: string) => {
  if (registry[gm][coach]) {
    return {
      success: true,
      message: registry[gm][coach]
    }
  }
  else {
    return {
      success: false,
      message: failMessages[Math.floor(Math.random() * failMessages.length)]
    }
  }
}
