import * as React from "react";
import {connect} from "react-redux";
import {stylesheet} from "typestyle";
import {setupCss} from "../helpers/setupCss";
import {Container as Cnt} from "react-bootstrap";
import {OpponentDeck} from './OpponentDeck'
import {Deck} from './Deck';
import {Table} from './Table';
import {IStore} from "../redux/IStore";
import {Dispatch} from "redux";
import {closeAction} from "../redux/modules/modalActionCreators";
import {Intro} from "../components/Intro";
import {Outro} from "../components/Outro";
import '../assets/dummy.css';

setupCss();


const classNames = stylesheet({
  container: {
    margin: 0,
    padding: 0,
    textAlign: "center"
  }
});

interface IStateToProps {
  showIntro: boolean;
  finished: boolean;
  finishedTitle?: string;
  finishedCaption?: string
}

interface IDispatchToProps {
  closeIntro: () => void
}

interface IProps extends IStateToProps, IDispatchToProps {}

class App extends React.Component<IProps> {

  public render(): JSX.Element {
    const {showIntro, closeIntro, finished, finishedTitle, finishedCaption} = this.props;
    return (
      <div>
        <section className={classNames.container}>
          <Cnt fluid className="align-items-center">
            <OpponentDeck/>
            <Table/>
            <Deck/>
          </Cnt>
          <Intro show={showIntro} close={closeIntro}/>
          <Outro show={finished} title={finishedTitle} caption={finishedCaption}/>
        </section>
      </div>
    );
  }
}

// const componentTranslationsSelector = createSelector(
//   translationsSelector,
//   (translations) => {
//     const translator: ITranslator = new Translator(translations);
//     return {
//       notFound: translator.translate("Not found")
//     };
//   }
// );

const mapStateToProps = (state: Pick<IStore, "intro" | "counter">): IStateToProps => {
  return {
    showIntro: state.intro.show,
    finished: state.counter.finished,
    finishedTitle: state.counter.message?.title,
    finishedCaption: state.counter.message?.caption
  }
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    closeIntro: () => dispatch(closeAction())
  };
};
const connected = connect(mapStateToProps, mapDispatchToProps)(App);

export {classNames, connected as App, App as UnconnectedApp, mapStateToProps};
