import * as React from "react";
import {Col} from 'react-bootstrap';

interface IOpponentCaptionProps extends React.HTMLProps<HTMLDivElement> {
  name: string;
  caption: string;
}

export class OpponentCaption extends React.Component<IOpponentCaptionProps> {
  public render(): JSX.Element {
    const {name, caption} = this.props;
    return (
      <Col xs={2} className="align-self-center">
        <p className="playing-card-title">{name}</p>
        <p className="playing-card-caption">{caption}</p>
      </Col>
    );
  }
}
